.nav{
    justify-content: space-between;
    display: flex;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    margin-bottom: 1.5rem;
}

.icon{
    font-size: 1.5rem;
    line-height: 2rem;
    cursor: pointer;
    vertical-align: middle;
}

.resume-button{
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    color: rgb(255 255 255 / 1);
    background-image: linear-gradient(to right, #06b6d4, #14b8b8);
    border-radius: 0.375rem;
    margin-left: 2rem;
    text-decoration: none;
}

.resume-button:hover{
    background-image: linear-gradient(to right, #039fbb, #0b9e8d);
}