.bodyHolder{
    text-align: center;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    padding: 2.5rem;
}

.name{
    font-size: 3.75rem;
    line-height: 1;
    font-weight: 500;
    padding: 0;
    margin-bottom: 0;
    margin-top: 0rem;
}

.job{
    font-size: 1.875rem;
    line-height: 1;
    font-weight: inherit;
}

.blurb{
    font-size: 1.25rem;
    line-height: 1.75rem;
    padding-bottom: 3rem;
    max-width: 25rem;
    margin-left: auto;
    margin-right: auto;
    font-weight: inherit;
}

.iconBox{
    color: rgb(156 163 175 / 1);
    font-size: 3rem;
    line-height: 1;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    gap: 4rem;
    justify-content: center;
    display: flex;
}

.bodyIcon{ vertical-align: middle; }
.bodyIcon:hover{ color: rgb(255 255 255 / 1); }
.bodyIconDark{ vertical-align: middle; }
.bodyIconDark:hover{ color: rgb(0 0 0 / 1); }

.pic{
    width: 24rem;
    height: 24rem;
    overflow: hidden;
    border-radius: 9999px;
    margin-top: 5rem;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    object-fit: cover;
    margin-bottom: 3rem;
}